	module.exports = {"data":{"markdownRemark":{"html":"<p>通常版  <strong>“<a href=\"/filez/music/tice.mp3\">justice -rapid style-”を聞く</a> (2:28 | [filesize]<a href=\"https://danmaq.com/filez/music/tice.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/tice.mp3[/filesize]</a>)</strong> 着メロ風  <strong>“<a href=\"/filez/music/tice_smaf_ma2.mp3\">justice -rapid style-” 着メロ風リミックスを聞く</a> (2:28 | [filesize]<a href=\"https://danmaq.com/filez/music/tice_smaf_ma2.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/tice<em>smaf</em>ma2.mp3[/filesize]</a>)</strong> Lapis Lazul様の曲 “Justice” のリミックス。ボーカルまできっちり収録。 原曲はBPM160と最初から早かったんですがそれを無理矢理BPM193まで高速化。ガチガチの超快速ユーロが作ってみたくてアレンジに挑戦してみました。今改めて聞くとやたら不協和音が含まれていたり等聞き苦しい部分もあるので、今度手が空いたら再リミックスしてみようかなと。</p>\n<h2>2009/9/7追記</h2>\n<p>BMS版はNORMAL譜面を追加し、従来譜面の難易度設定を7段階から12段階へ引きなおしました。旧難易度は7(ANOTHER:7)です。(従来譜面は殆ど弄っていません)</p>\n<h2>原作の著作権情報</h2>\n<ul>\n<li>Lapis Lazul / Justice</li>\n<li>cyber euro | 2:28 | 194BpM | BMSLEVEL:N8-H9-A10 | BMSNOTES:N1163-H1133-A1688</li>\n<li><a href=\"/filez/music/justice.zip\">BMS “justice -rapid style-”をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/music/justice.zip%5B/filesize%5D\">https://danmaq.com/filez/music/justice.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"2001-03-05T13:19:26+09:00","strDate":"2001/3/5","title":"[BMS][REMIX] justice -rapid style-"}}},"pathContext":{"path":"/ja/blog/2001/03/05-justice/","langKey":"ja"}}

